import { SET_HEADER } from '../actions';

const initialState = {};

export function headerReducer(state = initialState, action) {
  let newState = state;
  const { type, payload } = action;

  switch (type) {
    case SET_HEADER:
      newState = payload.header;
      break;
    default:
      break;
  }

  return newState;
}

export default headerReducer;
